/* 
This document contains the general styling for the Making Matters book.
Styles can be overwritten from the CSS view in the wiki.

https://pagedjs.org/documentation/
https://jamonserrano.github.io/plumber-sass/
*/
/* Plumber for baseline grid */
/* Paged and print specific styles are collected in paged.css */
/* Pagedjs/paged media specific styles */
@page {
  size: 105mm 297mm;
  margin-top: 15mm;
  margin-right: 10mm;
  margin-bottom: 25mm;
  margin-left: 10mm;
  bleed: 0;
  /* bleed: 6mm;
   marks: crop cross; */
  background-color: white;
  @footnote {
    float: bottom;
  }
  @bottom-center {
    content: counter(page);
  }
}
@page :left {
  margin-right: 15mm;
  margin-left: 10mm;
}
@page :right {
  @top-left {
    content: string(chapterTitle);
  }
  @top-right {
    content: string(articleTitle);
  }
}
@page :first {
  @top-left {
    content: "";
  }
  @bottom-right-corner {
    content: "";
  }
}
/* Setting variables */
h1,
.chapter-title {
  string-set: chapterTitle content(text);
}

h2 {
  counter-increment: countChapter;
  string-set: articleTitle content(text);
  counter-set: footnote-marker 0 footnote 0;
  string-set: author "";
}

span.author {
  string-set: author content(text);
}

/* Move the footnotes to the footer */
span.footnote {
  float: footnote;
}

/* Fonts & typography & baseline grid 
the baseline grid is tightly coupled (by Plumber) to 
the typeface so you find them combined in this file.

It intentionally verbose in declaring the styles to
make it easier to adapt. 
*/
@font-face {
  font-family: "Ortica";
  src: url("../fonts/Ortica-Font/Ortica-Light.woff") format("woff");
  font-weight: 300;
}
@font-face {
  font-family: "Ortica";
  src: url("../fonts/Ortica-Font/Ortica-Bold.woff") format("woff");
  font-weight: 400;
}
.font-ortica {
  font-family: Ortica, serif;
  font-weight: 300;
}

@font-face {
  font-family: "Combine";
  src: url("../fonts/Combine/Combine.otf") format("opentype");
  font-weight: 300;
}
.font-combine {
  font-family: "Combine", serif;
  font-weight: 300;
}

/* Fonts */
@font-face {
  font-family: "Authentic";
  src: url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-60.woff2") format("woff2"), url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-60.woff") format("woff");
  font-weight: 60;
}
@font-face {
  font-family: "Authentic";
  src: url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-90.woff2") format("woff2"), url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-90.woff") format("woff");
  font-weight: 90;
}
@font-face {
  font-family: "Authentic";
  src: url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-130.woff2") format("woff2"), url("../fonts/AUTHENTIC_SANS/woff/AUTHENTICSans-130.woff") format("woff");
  font-weight: 130;
}
.font-authentic {
  font-family: Authentic, sans-serif;
  font-weight: 90;
}

/* Fonts */
@font-face {
  font-family: "Symbola";
  src: url("../fonts/Symbola.ttf") format("truetype");
}
.font-symbola {
  font-family: "Symbola", sans-serif;
}

body {
  font-family: Ortica, serif;
  font-weight: 300;
}

h1 {
  page-break-before: left;
}

h2 {
  page-break-before: right;
}

.author {
  page-break-after: always;
}

.title-page {
  page-break-before: right;
}

.empty-left-page {
  page-break-before: left;
}

.full-spread-image-section {
  page-break-before: left;
}

.full-page-image {
  page-break-before: always;
}

.full-page-image.full-page-image-left {
  page-break-before: left;
}

.full-page-image.full-page-image-right {
  page-break-after: always;
}

.full-page-image {
  width: var(--pagedjs-width);
  height: var(--pagedjs-height);
  overflow: hidden;
  left: calc((var(--pagedjs-bleed-left) + var(--pagedjs-margin-left)) * -1);
  top: calc((var(--pagedjs-bleed-top) + var(--pagedjs-margin-top)) * -1);
  position: absolute;
}

.full-page-image div {
  width: calc(var(--pagedjs-width) * 2 - var(--pagedjs-bleed-left) - var(--pagedjs-bleed-right));
  height: var(--pagedjs-height);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

/* compensate for bleeds */
.pagedjs_right_page .full-page-image div {
  margin-left: calc(-1 * var(--pagedjs-width) + var(--pagedjs-bleed-left) + var(--pagedjs-bleed-right));
}

.full-page-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin: 0 !important;
}

.title-spread .full-page-image {
  background-color: black;
}

.full-spread-image-section .full-page-image div.full-spread-image-caption {
  height: auto;
  position: absolute;
  display: block;
  right: calc(var(--pagedjs-bleed-right) + var(--pagedjs-margin-right));
  bottom: calc(var(--pagedjs-bleed-bottom) + var(--pagedjs-margin-bottom));
  width: calc(var(--pagedjs-width) - var(--pagedjs-bleed-right) - var(--pagedjs-margin-right) - var(--pagedjs-bleed-left) - var(--pagedjs-margin-left));
}

.pagedjs_page {
  position: relative;
}